@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap);

.nav_container {
  height: 80px;
  margin: 20px auto;
  background: red;
  max-width: 1400px;
}
/* 미디어 쿼리 */
.nav_frame {
  height: 80px;
  margin: auto;
  background: orange;
  position: relative;
  padding: 0 20px;
  box-sizing: border-box;
}
.logo_container {
  width: 240px;
  height: 80px;
  float: left;
  background: yellow;
}
.logo_container > div {
  width: 236px;
  height: 76px;
  margin: 2px;
  background: green;
}
@media screen and (min-width: 1024px) {
  .nav_frame {
    min-width: 1024px;
    max-width: 1400px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .nav_frame {
    min-width: 768px;
    max-width: 1023px;
  }
}
@media screen and (max-width: 767px) {
  .nav_frame {
    width: 440px;
  }
  .logo_container {
    width: 0px;
  }
}

/* 체크박스 */
.nav_frame > input[type="checkbox"] {
  display: none;
}
/* 왼쪽 메뉴 공간 */
.menuicon_container {
  width: 80px;
  height: 80px;
  float: left;
  position: relative;
  background: yellow;
}

.menuicon_container > label {
  width: 40px;
  height: 40px;
  display: block;
  position: absolute;
  top: 20px;
  left: 20px;
  cursor: pointer;
  z-index: 5;
  background: green;
}
.menuicon_container > label > span {
  width: 20px;
  height: 4px;
  display: block;
  position: absolute;
  left: 10px;
  background: #aeaeae;
}
.menuicon_container > label > span:nth-child(1) {
  top: 7px;
}
.menuicon_container > label > span:nth-child(2) {
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.menuicon_container > label > span:nth-child(3) {
  bottom: 7px;
}
/* 왼쪽 메뉴 공간 애니메이션 */
input[type="checkbox"]:checked + .menuicon_container > label > span:nth-child(1) {
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(45deg);
          transform: translateY(-50%) rotate(45deg);
}
input[type="checkbox"]:checked + .menuicon_container > label > span:nth-child(2) {
  display: none;
}
input[type="checkbox"]:checked + .menuicon_container > label > span:nth-child(3) {
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(135deg);
          transform: translateY(-50%) rotate(135deg);
}
/* 로고 공간 */
.nav_menu_container {
  width: 320px;
  height: 80px;
  float: right;
  background: yellow;
}
.nav_menu_container > ul > li {
  width: 60px;
  height: 60px;
  display: inline-block;
  background: green;
  margin: 10px;
  cursor: pointer;
}
/* 사이드 메뉴 목록 공간 */
.side_menu_container {
  display: none;
  z-index: 2;
}
input[type="checkbox"]:checked ~ .side_menu_container {
  width: 320px;
  height: 800px;
  background: rgb(146, 191, 209);
  position: absolute;
  display: block;
}
.top {
  width: 40px;
  height: 40px;
  position: fixed;
  right: 0;
  bottom: 0;
  background: gold;
  display: block;
}

.side_menu_list {
  font-size: 30px;
  line-height: 60px;
  margin-top: 170px;
  margin-left: 10px;
  cursor: pointer;
}

.menu-item:hover {
  color: white;
  font-weight: 800;
  box-shadow: rgba(119, 119, 119, 0.4);
  display: block;
  background: rgba(43, 174, 102);
}

.each_menu_container {
  width: 300px;
  height: 40px;
  margin-bottom: 24px;
  line-height: 30px;
  font-size: 25px;
  font-weight: 800;
  margin-left: 10px;
  color: #333333;
}

.menu {
  color: white;
}

/* CSSTransition classes  */
.menu-first-enter {
  position: absolute;
  -webkit-transform: translateX(110%);
          transform: translateX(110%);
}
.menu-first-enter-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 1s ease;
}
.menu-first-exit {
  position: absolute;
}
.menu-first-exit-active {
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  transition: all 1s ease;
}

.menu-second-enter {
  -webkit-transform: translateX(110%);
          transform: translateX(110%);
}
.menu-second-enter-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 1s ease;
}
.menu-second-exit-active {
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  transition: all 1s ease;
}

.menu-third-enter {
  -webkit-transform: translateX(110%);
          transform: translateX(110%);
}
.menu-third-enter-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 1s ease;
}
.menu-third-exit-active {
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  transition: all 1s ease;
}

