.arrow_button {
  opacity: 0.5;
  background: transparent;
  display: inline-block;
  font-size: 30px;
  padding: 0px 2px;
}

.left_arrow_button {
  float: left;
}

.right_arrow_button {
  float: right;
}

.arrow_button:active {
  color: white;
}

.show_page_num {
  background: #777777;
  position: absolute;
  box-sizing: border-box;
  min-height: 2vw;
  padding: 0px 5px;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  display: inline-block;
  text-align: center;
  border-radius: 10px;
}

.copybutton {
